import React, { Component } from 'react'
import Task from './Task';

export default class TodoItems extends Component {
    createTask(item, handleDelete){
        console.log(item);
        
    }
  
    render() {
        const todoTasks = this.props.tasks;
        const deleteItem = this.props.deleteItem;
        const listItems = todoTasks.map(function(e,index){
            return <Task key={index} index={index} item={e} deleteItem={deleteItem}/>
        });
        return (
        <div className="">
            <p className="uk-text-danger">Tareas</p>
            <table className="uk-table">
                <tbody>
                    {listItems}
                </tbody>
            </table>
        </div>
        )
    }
}
