import React, { Component } from 'react';
import Moment from 'react-moment';
import 'moment-timezone'; 

import NavBar from './Components/NavBar';
import AddTask from './Components/AddTask';
import TodoItems from './Components/TodoItems';


import './App.css';

const db = {
    "Tasks": [
        {
            "name": "Hacer la tarea",
            "completed": false
        },
        {
            "name": "Comprar leche",
            "completed": false
        },
        {
            "name": "Comprar comida",
            "completed": false
        }
    ]
}

class App extends Component {
    constructor(){
        super();
        this.state = {
            tasks: []
        }

        this.deleteItem = this.deleteItem.bind(this);
        this.addItem = this.addItem.bind(this);
    }

    componentDidMount(){
        this.setState({
            tasks: db.Tasks
        })
    }

    addItem = (e,item) => {
        e.preventDefault();

        const newItem = {
            "name": item,
            "completed": false
        }

        console.log(newItem);
        const items = [...this.state.tasks, newItem];
        this.setState({
            tasks: items
        })

    }

    deleteItem = key => {
        console.log("Delete: " + key);
        
        const copy = this.state.tasks;
        copy.splice(key,0);
        this.setState({
          tasks: copy
        })
        console.log("State Delete: " + this.state.tasks);
    }

    render() {
    return (
      <div className="App">
        <NavBar/>
        <div className="uk-section">
          <div className="uk-container">

              <h3>El día de hoy</h3>

              <div className="uk-grid-match uk-child-width-1-4@m uk-grid">
                <div>
                    <div className="uk-width-1-2@s uk-width-2-5@m">
                        <ul className="uk-nav uk-nav-default">
                            <li className="uk-active"><p>Hoy</p></li>
                            <li><p className="isDisabled">Mañana</p></li>
                            <li><p className="isDisabled">Siguiente Semana</p></li>
                        </ul>
                    </div>
                </div>
                <div>
                    <Moment format="MMM Do YY" style={{marginBottom: '8px'}}></Moment>
                    <TodoItems tasks={this.state.tasks} deleteItem={this.deleteItem}/>
                    <AddTask addItem={this.addItem}/>
                </div>
              </div>

          </div>
      </div>
      </div>
    );
  }
}

export default App;
