import React, {Component} from 'react';

class AddTask extends Component {
  constructor() {
    super();
    this.state = {
      shown: false,
      task: "",
      disabled: true
    }

    this.toggle = this.toggle.bind(this);
    this.handleTask = this.handleTask.bind(this);
    this.handleClick = this.handleClick.bind(this);
  }

  toggle(){
    this.setState({
      shown: !this.state.shown  
    })
  }

  handleTask(e){
    console.log("start" + this.state.task);
    if(e.target.value !== ""){
      this.setState({
        task: e.target.value,
        disabled: false
      })
    }else{
      this.setState({
        disabled: true
      })
    }
    console.log("End" + this.state.task);
  }

  handleClick(e){
    const addItem = this.props.addItem;
    if(this.state.task !== undefined){
      addItem(e,this.state.task);
      this.setState({
        task: "",
        disabled: true
      })
      this.toggle();
    }

  }

  render(){
    const shown = this.state.shown;
    
    return (
      <div>
        {shown ? 
        (
          <div>
            <form>
              <div className="uk-margin">
                <div className="uk-form-controls">
                  <input onChange={(e) => this.handleTask(e)} className="uk-input" type="text" placeholder="p. ej. Hacer la tarea"/>
                </div>
                <button disabled={this.state.disabled} className="uk-button uk-button-primary" onClick={(e) => this.handleClick(e)}
                style={{marginTop: '8px'}}>
                  Añadir
                </button>
                <button className="uk-button uk-button-text" onClick={this.toggle}
                style={{marginTop: '8px', marginLeft: '8px'}}>
                  Cancelar
                </button>
              </div>
            </form>
          </div>
        )
        :
        (
          <div style={{display: "inline"}}>
            <button className="uk-button uk-button-text" onClick={this.toggle}>
              <span className="uk-margin-small-right" uk-icon="plus">
              </span><span>Añadir tarea</span>
            </button>
          </div>
        )}
      </div>      
    )
  }
}

export default AddTask
