import React, { Component } from 'react'

export default class Task extends Component {
    constructor(props){
        super(props);
        this.state = {
            name: props.item.name,
            complete: props.item.completed,
        }

        this.handleComplete = this.handleComplete.bind(this);
        this.handleDelete = this.handleDelete.bind(this);
    }

    handleComplete(){
        this.setState({
            complete: !this.state.complete
        })
    }

    handleDelete(e){
        this.props.delete(this.state.name);
    }

    render() {
        return (
            <tr>
                <td>
                    <button className="uk-icon uk-inline" onClick={this.handleComplete}>
                        {this.state.complete ? 
                            (
                                <span className="uk-icon" uk-icon="bolt" style={{color:"green"}} ></span>
                            ) :
                            (
                                <span className="uk-icon" uk-icon="bolt"></span>
                            )
                        }
                    </button>
                </td>
                <td>
                    {this.state.complete ? 
                        (
                            <p style={{textDecorationLine:"line-through"}}>{this.state.name}</p>
                        ):
                        (   
                            <p>{this.state.name}</p>
                        )
                    }
                </td>
                <td>
                    <button className="uk-icon" uk-icon="trash" style={{color:"red"}} onClick={(e) => this.props.deleteItem(this.props.index)}>
                    </button>
                </td>
            </tr>
        )
    }
}
